<script setup>
import { ref } from 'vue'
import router from '@/router'
const MenuList = ref([
  [
    { name: '翻译网站', express: 'translate' },
    { name: '骰子模拟器', express: 'diceRoll' },
    { name: '资源站', express: 'sourceData' }
  ]
])

const chooseTool = (express) => {
  router.push({ name: express })
}
</script>

<template>
  <div class="container">
    <el-carousel indicator-position="none" arrow="never" height="100px">
      <el-carousel-item v-for="menus in MenuList" :key="menus">
        <div class="el-carousel-container">
          <div class="menu" v-for="menu in menus" :key="menu" @click="chooseTool(menu.express)">
            {{ menu.name }}
          </div>
        </div>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<style scoped>
.container {
  border-radius: 10px;
  overflow: hidden;
  width: 800px;
  border: 1px solid #e9f2fc;
}

.el-carousel-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 100%;
}

.el-carousel__item .menu {
  color: #475669;
  opacity: 0.75;
  margin: 0;
  text-align: center;
  background-color: #f6f7f8;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

.el-carousel__item .menu:hover {
  color: #00aeec;
}

.el-carousel__item:nth-child(n) {
  background-color: #e3e5e7;
}
</style>
